ta, la pelcula bacteriana crece espontneamente y los slidos en suspensin a la salida del biodisco disminuyen. Si la carga disminuye, la biomasa se desprende y aumentan los slidos en el efluente al decantador secundario. No es necesario controlar la concentracin del licor de mezcla, ya que el fango extrado es un desecho del proceso y ha de eliminarse como mejor convenga.")])])]),_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"titulo":"Ruidos y otras variables"}},[_c('div',{staticClass:"col-md-2 mb-4 mb-md-0"},[_c('img',{attrs:{"src":require("@/assets/curso/images/tema2/img_new_6.svg")}})]),_c('div',{staticClass:"col-md-10 mb-4 mb-md-0"},[_c('p',{staticClass:"mb-3"},[_vm._v(" El nivel de ruidos es muy bajo, factor que es muy importante tanto ambientalmente como por higiene y salud del personal. Las dimensiones de los depsitos de oxidacin son menores que los utilizados por otros procesos. El rendimiento del proceso es ms estable en pocas fras, debido al hecho de permanecer cubiertos los tanques. El consumo energtico es muy reducido y a igualdad de resultados, comparndolo con un sistema de fangos activos, el consumo resulta ser la tercera parte Hidrometalica (s.f.).")])])]),_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"titulo":"El funcionamiento de los biodiscos"}},[_c('div',{staticClass:"col-md-2 mb-4 mb-md-0"},[_c('img',{attrs:{"src":require("@/assets/curso/images/tema2/img_new_7.svg")}})]),_c('div',{staticClass:"col-md-10 mb-4 mb-md-0"},[_c('p',{staticClass:"mb-3"},[_vm._v(" Se divide en cuatro etapas, ver figura 30.")])])]),_c('div',{staticClass:"row justify-content-center align-items-center",attrs:{"titulo":"El agua bruta"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',{staticClass:"mb-3"},[_vm._v(" El agua bruta de llegada entra a este tanque a travs de la tubuladura de entrada, donde se produce una primera sedimentacin de materia slida. Los slidos sedimentados se extraern peridicamente por parte del usuario en la primera etapa donde se realiza la primera toma de contacto entre el agua bruta de entrada y los discos. La superficie de los discos se coloniza de forma natural, formando un recubrimiento visible. En la segunda etapa de contacto, gracias a la concepcin del sistema de vertedero entre ambas etapas, se consigue un nivel de agua con menos fluctuaciones, favoreciendo el proceso y su salida hacia el tanque de sedimentacin secundaria. Con el proceso de dos etapas se consigue aumentar el crecimiento de la biomasa de forma equilibrada y constante. En el tanque de sedimentacin secundaria se produce la sedimentacin de la materia slida restante del proceso, a la cual se le realizan dos operaciones, la recirculacin y extraccin de sedimentos (Hidrometlica, s.f.). ")])])])])],1)]),_vm._m(62),_c('p',{staticClass:"mb-5"},[_vm._v("Son reactores discontinuos en los que el agua residual se mezcla con un lodo biolgico en un medio aireado. Se trata de un proceso que se combina en un mismo tanque: aeracin/reaccin y clarificacin. La tecnologa de los reactores biolgicos secuenciales es una variante optimizada de la tecnologa convencional de lodos activados. Se basa en el uso de un solo reactor que opera en forma discontinua secuencial. El sistema de los reactores biolgicos secuenciales SBR consta de al menos cuatro procesos cclicos: llenado, aireacin, anoxia, decantacin y vaciado, tanto de efluente como de los lodos. Esta tecnologa es capaz de tolerar variaciones de carga y caudal y genera como producto lodos estabilizados, siendo en ocasiones, la tecnologa ms apropiada para la industria, ver figura 31 y 32. ")]),_vm._m(63),_c('p',{staticClass:"mb-3"},[_vm._v("Se debe considerar que el tratamiento de aguas residuales mediante SBR se lleva a cabo en secuencia en 4 etapas:")]),_c('div',{staticClass:"row justify-content-center align-items-center my-3"},[_c('div',{staticClass:"col-12 col-lg-12"},[_c('SlyderF',{staticClass:"slyder-color mb-5",attrs:{"columnas":"col-lg-6 c