      h1 Sistemas para el tratamiento fsico, qumico y biolgico del agua residual


    p.mb-3 <b>El control de las aguas residuales se basa mayormente en tratamientos fsicos, qumicos y biolgicos que depuran las concentraciones contaminantes presentes en un vertimiento originado de manera antrpica.</b> Es por ello por lo que existen las plantas de tratamiento de aguas residuales PTAR, las cuales cuentan con un tren de tratamiento dividido por unidades primarias, secundarias y terciarias. En cada una de estas unidades de tratamiento se eliminan las concentraciones contaminantes de acuerdo con su diseo y funcionamiento. Estos a su vez cuentan con una eficiencia de tratamiento, que es la que permite entregar en el efluente el agua en buenas condiciones. 

