        .jumbotron.p-5.h-100 
          p.mb-3 Es as como los tratamientos fsicos se concentran en remover principalmente slidos, partiendo del diseo de su estructura. Por otro lado, el tratamiento qumico se da a travs de la dosificacin de un agente o reactivo que de acuerdo con las necesidades de tratamiento del agua puede coadyuvar a sedimentar slidos, desinfectar o hasta precipitar metales pesados. Por ltimo, el tratamiento biolgico es el encargado de remover la carga orgnica que contiene el agua residual, que por lo general por accin de ciertos microorganismos depuran el agua residual en su proceso metablico y de digestin.

