    h4.mb-3 Las rejillas tambin operan de manera manual

    p.mb-3 Las rejas de desbaste de limpieza manual son las ms utilizadas por su economa, ya que el usuario deber limpiar peridicamente la reja manualmente con un rastrillo y eliminar los slidos atrapados. Las rejas de desbaste de limpieza automtica se caracterizan por su funcionamiento mecnico. Atrapan los slidos ms gruesos y a travs de un cepillo o brazo hidrulico las envan a un contenedor para su reciclado (Deagua, 2020).

