                h4 Desarenadores de flujo horizontal
                p.mb-0 Consiste en reducir la velocidad de flujo del agua, con el fin de que las partculas slidas que se encuentran suspendidas se decanten y se depositen en el fondo. Esto se logra ensanchando el canal en un tramo, en donde existirn canales paralelos que ayuden en la limpieza de las partculas depositadas. Se debe medir el caudal y la velocidad mediante un canal parshall para mantener controlado el flujo de agua.
          .tarjeta.back-tab.px-5.py-4(titulo="Desarenadores de flujo vertical")
            .row.px-2.py-2.justify-content-center
              .col-4.col-lg-2
                img(src='@/assets/curso/images/tema2/img_7.svg').mb-3
              .col-lg-10
                h4 Desarenadores de flujo vertical
                p.mb-0 Son sistemas que recogen las aguas residuales de un conjunto de viviendas que normalmente estn ubicadas en un rea inferior a 1 ha. mediante tramos simplificados, para ser conducidas a la red de alcantarillado municipal o eventualmente a una planta de tratamiento. 
          .tarjeta.back-tab.px-5.py-4(titulo="Desarenadores de flujo inducido")
            .row.px-2.py-2.justify-content-center
              .col-4.col-lg-2
                img(src='@/assets/curso/images/tema2/img_7.svg').mb-3
              .col-lg-10
                h4 Desarenadores de flujo inducido
                p.mb-0 Estos desarenadores tienen forma rectangular y en ellos se inyecta aire mediante aireadores tipo turbina para producir el movimiento del agua en forma de espiral, lo cual produce la decantacin de los slidos suspendidos. Este proceso tambin favorece la deposicin de la materia orgnica. Posteriormente, se lleva a cabo la separacin de las arenas de forma manual o mecanizada, de acuerdo con el tamao de la planta, ver figura 7.
         
    .row.justify-content-center.align-items-star.mb-3
      .col-12.col-lg-10(data-aos="fade-left") 
