          p.text-white.text-center.mb-0 <b>Desengrasado</b>


    p.mb-3 Despus de cualquiera de los tres procesos de desarenado se tiene que efectuar un desengrasado para separar las grasas y aceites de los canales, previniendo que los siguientes procesos se vean afectados. Aunque existen desarenadores que llevan el proceso de desengrasado de manera conjunta con el desarenado mediante aire comprimido (Domos agua, 2018), ver figura 8.

    .row.justify-content-center.align-items-star.mb-3
      .col-12.col-lg-10(data-aos="fade-left") 
          .titulo-sexto.color-acento-contenido.mb-3
            h5 Figura 8
            span Desarenado, desengrasado y elevacin
          img(src='@/assets/curso/images/tema2/fig_3.svg').mb-3

    p.mb-3 De acuerdo con la empresa Cyclusid el desengrasado consiste en la separacin de las grasas y aceites arrastrados por el agua residual. Las grasas en las aguas residuales crean numerosos problemas en el proceso de depuracin, entre los que se destacan:

    .row.justify-content-center.align-items-center.mb-3
      .col-12.col-lg-10(data-aos="fade-left") 
        .jumbotron.p-4
          .row.justify-content-between
            .col-4.col-lg-2.ps-0.pe-0
