                li 
                  .lista-ol--cuadro__vineta
                    span(style='color:black') 1
                  p.mb-0 Adhesin a aparatos, conductos o depsitos, dificultando la depuracin.
                li 
                  .lista-ol--cuadro__vineta
                    span(style='color:black') 2
                  p.mb-0  Obstruccin de las rejillas finas.
                li 
                  .lista-ol--cuadro__vineta
                    span(style='color:black') 3
                  p.mb-0  Formacin de una capa en la superficie de los decantadores, que dificulta la sedimentacin al atraer hacia arriba pequeas partculas de materia orgnic
                li 
                  .lista-ol--cuadro__vineta
                    span(style='color:black') 4
                  p.mb-0  Dificulta la correcta aireacin en la depuracin de fangos activos.
          
    .row.align-items-center.mb-5
      .col-lg-2(data-aos="fade-left") 
        .jumbotron-1.p-2
          p.text-white.text-center.mb-0 <b>Homogenizacin</b>

    p.mb-3 La situacin ms habitual es que la entrada de agua residual a la planta depuradora sea variable en el tiempo, tanto en lo que respecta al caudal como a la carga contaminante. Estas variaciones, especialmente las descargas, dificultan el correcto desarrollo de los tratamientos, ya sean de tipo fisicoqumico o biolgico. Para solventar tales problemas se puede recurrir a la igualacin de caudales y/o a la homogeneizacin de la concentracin de contaminantes, es decir, optimizar las condiciones operativas de las fases siguientes (Cyclusid depuracin industrial, s.f.), ver figura 9. 

    .row.justify-content-center.align-items-star.mb-3
      .col-12.col-lg-8(data-aos="fade-left") 
          .titulo-sexto.color-acento-contenido.mb-3
            h5 Figura 9
            span Tanque de homogenizacin
          img(src='@/assets/curso/images/tema2/fig_4.svg').mb-3

    p.mb-3 En determinadas plantas industriales la composicin del agua residual de alimentacin a la planta de tratamiento vara de forma notable a lo largo del da, o bien se producen descargas puntuales con altas concentraciones de sus contaminantes. Si estas variaciones son importantes es preciso llevar a cabo una homogeneizacin del mismo, con el fin de que la planta trabaje en las mejores condiciones posibles (Inditex, 2014). 

