    #t_2_2.titulo-segundo
      .h2 2.2 Tratamientos primarios del agua residual

    p.mb-3 Son aquellos que eliminan los slidos en suspensin presentes en el agua. Los principales procesos fisicoqumicos que pueden ser incluidos en el tratamiento primario son:


    .row.justify-content-center.align-items-star.mb-3
