        .jumbotron.p-5.h-100 
          p.mb-3 <b>Es un proceso de separacin por gravedad que hace que una partcula ms densa que el agua tenga una trayectoria descendente, depositndose en el fondo del sedimentador. </b> Est en funcin de la densidad del lquido, del tamao, del peso especfico y de la morfologa de las partculas (Acuatecnia S.A.S, 2018), ver figuras 10 y 11.
