    p.mb-3 De acuerdo con Maldonado (s.f.), los tipos de sedimentacin se pueden clasificar como:

    .row.align-items-center.align-items-center.my-5
      .col-lg-12
        TabsA.color-acento-contenido(data-aos="fade")
          .tarjeta.back-tab.px-5.py-4(titulo="Sedimentacin de partculas discretas")
            .row.px-2.py-2.justify-content-center
              .col-4.col-lg-2
                img(src='@/assets/curso/images/tema2/img_7.svg').mb-3
              .col-lg-10
                h4 Sedimentacin de partculas discretas
                p.mb-0 Son aquellas partculas que no cambian de caractersticas (forma, tamao, densidad) durante la cada. Se denomina sedimentacin o sedimentacin simple al proceso de depsito de partculas discretas. Este tipo de partculas y esta forma de sedimentacin se presentan en los desarenadores, en los sedimentadores y en los presedimentadores como paso previo a la coagulacin en las plantas de filtracin rpida y tambin en sedimentadores como paso previo a la filtracin lenta. 
          .tarjeta.back-tab.px-5.py-4(titulo="Sedimentacin de partculas floculentas")
            .row.px-2.py-2.justify-content-center
              .col-4.col-lg-2
                img(src='@/assets/curso/images/tema2/img_7.svg').mb-3
              .col-lg-10
                h4 Sedimentacin de partculas floculentas
                p.mb-0 Partculas floculentas son aquellas producidas por la aglomeracin de partculas coloides desestabilizadas a consecuencia de la aplicacin de agentes qumicos. A diferencia de las partculas discretas, las caractersticas de este tipo de partculas forma, tamao, densidad s cambian durante la cada; se denomina sedimentacin floculenta o decantacin al proceso de depsito de partculas floculentas. Este tipo de sedimentacin se presenta en la clarificacin de aguas como proceso intermedio entre la coagulacin-floculacin y la filtracin rpida. 
          .tarjeta.back-tab.px-5.py-4(titulo="Sedimentacin por cada libre e interferida")
            .row.px-2.py-2.justify-content-center
              .col-4.col-lg-2
                img(src='@/assets/curso/images/tema2/img_7.svg').mb-3
              .col-lg-10
                h4 Sedimentacin por cada libre e interferida
                p.mb-0 Cuando existe una baja concentracin de partculas en el agua, estas se depositan sin interferir y se denomina a este fenmeno cada libre. En cambio, cuando hay altas concentraciones de partculas se producen colisiones que las mantienen en una posicin fija y ocurre un depsito masivo en lugar de individual. A este proceso de sedimentacin se le denomina depsito o cada interferida o sedimentacin zonal. Cuando las partculas ya en contacto forman una masa compacta que inhibe una mayor consolidacin se produce una compresin o zona de compresin. Este tipo de sedimentacin se presenta en los concentradores de lodos de las unidades de decantacin con manto de lodos.
    
