          p.mb-0 Nota. Tomado de Pedraza (2020). 

    h3.mb-3 Los sedimentadores y su clasificacin 

    p.mb-3 Los sedimentadores pueden ser tratamientos primarios o secundarios dependiendo de su ubicacin dentro del tren de tratamiento, esto depende de las necesidades de tratamiento de acuerdo con las caractersticas del agua residual inicial. 

    p.mb-3 El siguiente esquema ilustra las diferencias entre ambos sedimentadores:

