        .jumbotron-4.p-5.h-100 
          p.mb-3 <b>Es un proceso fsico fundamentado en la diferencia de densidades.</b> La flotacin permite separar la materia slida o lquida de menor densidad que la del fluido, por ascenso de esta hasta la superficie del fluido, ya que en este caso las fuerzas que tiran hacia arriba (rozamiento y empuje del lquido) superan a la fuerza de la gravedad. Se generan pequeas burbujas de gas (aire), que se asociarn a las partculas presentes en el agua y sern elevadas hasta la superficie, donde son arrastradas y sacadas del sistema (Acuatecnia S.A.S., 2018), ver figura 15.
