              .col-md-8.mb-4.mb-md-0
                h4 En qu consiste la flotacin?     
                p En unir las burbujas de aire al material suspendido, causando una reduccin neta del peso especfico. Las burbujas de tamao micromtrico se producen por disolucin del aire en el agua residual a presiones elevadas, seguida por su posterior liberacin a la presin atmosfrica. Cuando la presin se reduce a las condiciones atmosfricas el aire disuelto presente en exceso hasta la saturacin es liberado en forma de burbujas extremadamente finas que se adhieren a las materias en suspensin. 
            
              
        
