    .row.justify-content-center.align-items-star.mb-5
      .col-lg-10(data-aos="fade-left") 
        p.mb-3 Qu es el sistema de flotacin por aire inducido?     

        p.mb-3 Cuando el objetivo no es disponer de una gran eficiencia y calidad del agua y, la necesidad es disponer de un equipo robusto, fcil de utilizar, de bajo mantenimiento y econmico, existe el sistema de flotacin por aire inducido. En este el corazn del sistema es el inductor especial: el aireador impulsor gira creando un remolino y una depresin dentro de la celda que produce una aspiracin del aire a travs del eje hueco. El aire penetra en el lquido al llegar a la celda de aireacin, mediante grandes cantidades de micro burbujas. La mezcla rica en aire es liberada en la celda de flotacin facilitando la separacin de fases (Aguas Sigma, 2016).

    .row.align-items-center.mb-5
      .col-lg-3(data-aos="fade-left") 
        .jumbotron-1.p-2
          p.text-white.text-center.mb-0 <b>Coagulacin-floculacin.</b>

    
    .row.justify-content-center.align-items-star.mb-5
      .col-lg-5(data-aos="fade-right").mb-3 
        .jumbotron.p-5.h-100 
          p.mb-3 En muchos casos parte de la materia en suspensin est formada por partculas de muy pequeo tamao, lo que conforma una suspensin coloidal. Estas suspensiones coloidales suelen ser muy estables, en muchas ocasiones debido a interacciones elctricas entre las partculas. Por tanto, tienen una velocidad de sedimentacin extremadamente lenta, por lo que hara inviable un tratamiento mecnico clsico. Una forma de mejorar la eficacia de todos los sistemas de eliminacin de materia en suspensin es la adicin de ciertos reactivos qumicos que, en primer lugar, desestabilicen la suspensin coloidal (coagulacin) y a continuacin favorezcan la floculacin de estas para obtener partculas fcilmente sedimentables (Acuatecnia S.A.S., 2018), ver figura 17.
      .col-12.col-lg-5(data-aos="fade-left")
        .titulo-sexto.color-acento-contenido.mb-3
            h5 Figura 17
            span Floculadores
        img(src='@/assets/curso/images/tema2/fig_10.svg').mb-3      


    .row.justify-content-center.align-items-center.my-5.p-3
      .tarjeta.bg_grad-02.p-4.mb-5(data-aos="zoom-in-up")
        SlyderA(tipo="b")
          .row.justify-content-center.align-items-center
            .col-lg-6
              h3.mb-5 Objetivo principal
              p.mb-3 De acuerdo con FRC systems, el objetivo principal de un floculador es mezclar los coagulantes y los floculantes con el agua residual y formar slidos que puedan flotar. Los componentes principales incluyen una estructura metlica, una entrada, vlvulas de muestreo, vlvulas de inyeccin, tuberas y una salida. Las tuberas estn configuradas de manera ascendente y serpenteante para reducir la cantidad de espacio necesario para brindar un tiempo de reaccin qumica suficiente (FRC systems, 2015). 
            .col-10.col-lg-6
              img(src="@/assets/curso/images/tema2/img_15.png").mb-3

          .row.justify-content-center.align-items-center
            .col-lg-6
              h3.mb-5 Mecanismo  funcionamiento     
              p.mb-3 Lo que ocurre dentro de un floculador es un concepto bastante simple, se bombea agua hacia del floculador, inmediatamente despus de la entrada se da inicio a la dosificacin qumica, generalmente coagulantes, mediante vlvulas de inyeccin. Ms adelante se inyecta un polmero floculante y se mezcla en las secciones subsiguientes del floculador. Algunas unidades incluyen un medidor de pH en lnea y puntos de inyeccin para agentes neutralizantes (FRC systems, 2015), ver figura 18.
            .col-10.col-lg-6
              img(src="@/assets/curso/images/tema2/img_16.png").mb-3

          .row.justify-content-center.align-items-center
            .col-lg-6
              h3.mb-5 Dosificacin qumica
              p.mb-3 Se puede variar de acuerdo con el tipo de floculante, pues permite que las partculas coloidales desestabilizadas o las partculas formadas durante la etapa de coagulacin se ensamblen en agregados y luego se puedan separar del medio acuoso; entre ellos se encuentran los floculantes catinicos y los aninicos (FRC systems, 2015).
            .col-10.col-lg-6
              img(src="@/assets/curso/images/tema2/img_17.png").mb-3


          .row.justify-content-center.align-items-center
            .col-lg-6
              h3.mb-5 Preparacin de los floculantes aninicos     
              p.mb-3 Los floculantes aninicos son preparados a base de copolmeros de acrilamida y cido acrlico, con carga inica negativa. Se unen con cargas catinicas residuales sobre coagulantes adsorbidos a coloides coagulados. Su poder floculante intrnseco aumenta con el peso molecular. Las poliacrilamidas tienen el peso molecular ms alto en el intervalo de 10-20 millones. Otros polmeros presentan propiedades especficas para condiciones especficas (Ingeniera Liquid technologies, s.f.). 
            .col-10.col-lg-6
              img(src="@/assets/curso/images/tema2/img_18.png").mb-3

          .row.justify-content-center.align-items-center
            .col-lg-6
              h3.mb-5 Floculantes catinicos         
              p.mb-3 Se basan en copolmeros de poliacrilamida que proporcionan una amplia gama de ionicidades adems de los pesos moleculares necesarios para lograr el mximo rendimiento en cada aplicacin. De acuerdo con Acoua tecnologa (2020), puede haber varias cargas en un floculante. Hay polmeros catinicos, basados en el nitrgeno, polmeros aninicos, basados en los iones del carboxilato y los polianfolitos, que llevan cargas positivas y negativas.
            .col-10.col-lg-6
              img(src="@/assets/curso/images/tema2/img_19.png").mb-3

          .row.justify-content-center.align-items-center
            .col-lg-6
              h3.mb-5 Desestabilizacin de partculas             
              p.mb-3 Los principales coagulantes y floculantes utilizados para desestabilizar las partculas y producir floculantes en agua son:
              .mx-3.ol.lista-ul
                li 
                  i.fas.fa-circle.c-check-1
                  p.mb-0 Sulfato de aluminio.
                li 
                  i.fas.fa-circle.c-check-1
                  p.mb-0  Aluminato de sodio.
                li 
                  i.fas.fa-circle.c-check-1
                  p.mb-0  Cloruro de aluminio.
                li 
                  i.fas.fa-circle.c-check-1
                  p.mb-0  Cloruro frrico.
                li 
                  i.fas.fa-circle.c-check-1
                  p.mb-0  Sulfato frrico.
                li 
                  i.fas.fa-circle.c-check-1
                  p.mb-0  Sulfato ferroso.
                li 
                  i.fas.fa-circle.c-check-1
                  p.mb-0  Polielectrolitos (Acoua tecnologa, 2020).
              
            .col-10.col-lg-6
              img(src="@/assets/curso/images/tema2/img_20.png").mb-3

    .row.align-items-center.mb-5
      .col-lg-3(data-aos="fade-left") 
        .jumbotron-1.p-2
          p.text-white.text-center.mb-0 <b>Coagulacin-floculacin.</b>

    p.mb-3 Es una operacin en la que se hace pasar el agua a travs de un medio poroso, con el objetivo de retener la mayor cantidad posible de materia en suspensin. El medio poroso tradicionalmente utilizado es un lecho de arena, de altura variable (Acuatecnia S.A.S., 2018), ver figura 19.


