        .jumbotron-5.ps-4.py-1
          p.mb-3 Operacin unitaria
        p.my-3 La filtracin como operacin unitaria para los procesos de tratamiento de aguas residuales es una prctica relativamente reciente. Actualmente la filtracin se emplea de modo generalizado para conseguir una mayor eliminacin de slidos en suspensin, de los efluentes de los procesos de tratamiento biolgicos y qumicos, tambin se utiliza para la eliminacin del fsforo precipitado por va qumica, y como etapa previa de un proceso de electrodilisis (Otero, 2006). 

      .row.mb-3
        .col-lg-8(data-aos="fade-right") 
          .jumbotron-6.ps-4.p-1
            p.mt-3 Formas de aplicacin de la filtracin

      p.mb-3 La filtracin puede efectuarse de muchas formas, con baja carga superficial (filtros lentos) o con alta carga superficial (filtros rpidos), en medios porosos (pastas arcillosas, papel de filtro) o en medios granulares (arena, antracita, granate o combinados), con flujo ascendente, descendente y mixto (parte ascendente y parte descendente). Por otro lado, el filtro puede trabajar a presin o por gravedad, segn sea la magnitud de la carga hidrulica que exista sobre el lecho filtrante (Otero, 2006).

      .row.mb-3
