    Separador 

    #t_2_3.titulo-segundo
      .h2 2.3 Tratamientos secundarios del agua residual

    p.mb-3 El tratamiento secundario de depuracin constituye una serie de importantes procesos de naturaleza biolgica de tratamiento de las aguas residuales que tienen en comn la utilizacin de microorganismos (entre las que destacan las bacterias) para llevar a cabo la eliminacin de materia orgnica biodegradable, tanto coloidal como disuelta, as como la eliminacin de compuestos que contienen elementos nutrientes (N y P) (Cyclusid depuracin industrial, s.f.).

    .row.justify-content-center.align-items-star.mb-3
      .col-lg-12
        .bloque-texto-g.color-custom-1.p-3.p-sm-4.p-md-5.mb-5.d-none.d-lg-block
          .bloque-texto-g__img(data-aos="fade-right")(
            :style="{'background-image': `url(${require('@/assets/curso/images/tema2/img_21.png')})`}"
          )
          .bloque-texto-g__texto.p-5.m-right(data-aos="fade-left")
            p.mb-0 <b>En la mayor parte de los casos la materia orgnica constituye la fuente de energa y de carbono que necesitan los microorganismos para su crecimiento. Adems, tambin es necesaria la presencia de nutrientes que contengan los elementos esenciales para el crecimiento,</b> especialmente los compuestos que contengan N y P, y por ltimo, en el caso de sistema aerobio, la presencia de oxgeno disuelto en el agua. Este ltimo aspecto ser clave a la hora de elegir el proceso biolgico ms conveniente (Cyclusid depuracin industrial, s.f.).

    .bloque-texto-a.color-custom-2.p-md-0.mb-5.d-block.d-lg-none
      .row.m-4.align-items-center.justify-content-between
        .col-lg-9(data-aos="fade-right")
          .bloque-texto-a__texto.p-5.my-5
            p.mb-0 <b>En la mayor parte de los casos la materia orgnica constituye la fuente de energa y de carbono que necesitan los microorganismos para su crecimiento.</b> Adems, tambin es necesaria la presencia de nutrientes que contengan los elementos esenciales para el crecimiento, especialmente los compuestos que contengan N y P, y por ltimo, en el caso de sistema aerobio, la presencia de oxgeno disuelto en el agua. Este ltimo aspecto ser clave a la hora de elegir el proceso biolgico ms conveniente (Cyclusid depuracin industrial, s.f.).
        .col-lg-3.mb-3.mb-lg-0(data-aos="fade-left")
