    .row.align-items-center.mb-5
      .col-lg-2(data-aos="fade-left") 
        .jumbotron-1.p-2
          p.text-white.text-center.mb-0 <b>Digestor aerbico.</b>

    p.mb-3 Es una operacin en la que se hace pasar el agua a travs de un medio poroso, con el objetivo de retener la mayor cantidad posible de materia en suspensin. El medio poroso tradicionalmente utilizado es un lecho de arena, de altura variable (Acuatecnia S.A.S., 2018), ver figura 19.4


    .row.justify-content-center.align-items-star.mb-5
      .col-lg-6(data-aos="fade-right").mb-3 
        .jumbotron.p-5.h-100 
          p.mb-3 Es un mtodo alternativo de tratar los lodos orgnicos producidos en el curso de las diversas operaciones de tratamiento. Los digestores aerbicos se pueden emplear para el tratamiento de: (1) nicamente lodos activados o de filtros percoladores; (2) mezclas de lodos activados o de filtros percoladores con lodos primarios, o (3) lodo biolgico en exceso de plantas de tratamiento de lodos activados sin sedimentacin primaria. Actualmente, suelen emplearse dos variantes del proceso de digestin aerobia: el sistema convencional y el sistema con oxgeno puro, aunque tambin se ha empleado la digestin aerobia termfila (Aguamarket, 2017), ver figura 21.
      .col-12.col-lg-6(data-aos="fade-left")
        .titulo-sexto.color-acento-contenido.mb-3
            h5 Figura 21
            span Tratamiento aerobio
        img(src='@/assets/curso/images/tema2/img_new_2.svg').mb-3    


    .row.align-items-center.align-items-center.my-5
      .col-lg-12
        TabsA.color-acento-contenido(data-aos="fade")
          .tarjeta.back-tab.px-5.py-4(titulo="Digestin con oxgeno y digestin aerbica termfila")
              .row.px-2.py-2.justify-content-center
                .col-lg-8.mb-3
                  h4 Digestin con oxgeno y digestin aerbica termfila     
                  p.mb-0 En los sistemas continuos, la decantacin y concentracin del lodo se realiza en un estanque independiente. La digestin con oxgeno puro es una modificacin del proceso de digestin aerobia en el que se sustituye el aire por oxgeno puro. El lodo que resulta es parecido al lodo que se obtiene en los procesos de digestin aerobia convencionales. La digestin aerobia termfila representa un refinamiento adicional del proceso de digestin aerobia. Este proceso puede permitir conseguir altos rendimientos de eliminacin de la fraccin biodegradable (superiores al 80%) en tiempos de detencin cortos (3 a 4 das), mediante la accin de bacterias termfilas a temperaturas entre 25 y 50 C superiores a la temperatura ambiente (Aguamarket, 2017).
                .col-12.col-lg-4
                  img(src='@/assets/curso/images/tema2/img_23.png').mb-3
          
          .tarjeta.back-tab.px-5.py-4(titulo="Digestin aerbica convencional")
            .row.px-2.py-2.justify-content-center
              .col-lg-8
                h4 Digestin aerbica convencional         
                p.mb-0 En esta el lodo se airea durante un largo perodo de tiempo en un estanque abierto, sin calefaccin, empleando difusores convencionales o aireadores superficiales. El proceso se puede llevar a cabo de manera continua o discontinua. En plantas de pequeo tamao se emplea el sistema discontinuo, en el que el lodo se airea y se mezcla completamente durante un largo perodo de tiempo, dejndose sedimentar a continuacin en el interior del mismo estanque (Aguamarket, 2017), ver figura 22.
              .col-12.col-lg-4
                img(src='@/assets/curso/images/tema2/img_22.svg').mb-3
          
    .row.align-items-center.mb-5
      .col-lg-2(data-aos="fade-left") 
        .jumbotron-1.p-2
          p.text-white.text-center.mb-0 <b>Digestor anaerobio.</b>


    .row.justify-content-center.align-items-star.mb-5
      .col-lg-6(data-aos="fade-right").mb-3 
        .jumbotron.p-5.h-100 
          p.mb-3 En este proceso se produce la descomposicin de la materia orgnica e inorgnica en ausencia de oxgeno molecular. Sus principales aplicaciones han sido y siguen siendo hoy en da, la estabilizacin de lodos concentrados producidos en el tratamiento del agua residual y de determinados residuos industriales; sin embargo, recientemente se ha demostrado que los residuos orgnicos diluidos tambin se pueden tratar anaerbicamente. En el proceso de digestin anaerobia, la materia orgnica contenida en la mezcla de lodos primarios y biolgicos se convierte biolgicamente, bajo condiciones anaerobias, en metano (CH4) y dixido de carbono (C02) (Aguamarket, 2017), ver figura 23
      .col-12.col-lg-6(data-aos="fade-left")
        .titulo-sexto.color-acento-contenido.mb-3
            h5 Figura 23
            span Digestin anaerobia
        img(src='@/assets/curso/images/tema2/img_new_3.png').mb-3 

    .row.align-items-star.justify-content-center.mb-5(data-aos="zoom-in-down")
      .col-lg-12
        TabsC.color-acento-contenido
          .py-3.py-md-4.color-tab.p-4.m-top-4.m-top-5(titulo="Dnde sucede el proceso?")
            .row
              .col-md-4
                  figure
                    img(src='@/assets/curso/images/tema2/img_25.png', alt='Texto que describa la imagen')
              .col-md-8.mb-4.mb-md-0
                h4 Dnde sucede el proceso?          
                p La conversin biolgica de la materia orgnica de los lodos parece que se produce en tres etapas. <b>El primer paso del proceso comporta la transformacin por va enzimtica (hidrlisis) de los compuestos de alto peso molecular en compuestos que puedan servir como fuentes de energa y de carbono celular.</b> El segundo paso (acidognesis) implica la conversin bacteriana de los compuestos producidos en la primera etapa en compuestos intermedios identificables de menor peso molecular. El tercer paso (metanognesis) supone la conversin bacteriana de los compuestos intermedios en productos finales ms simples, principalmente metano y dixido de carbono. En un digestor, la conversin de los lodos orgnicos y de los residuos se lleva a cabo mediante la accin conjunta de diferentes organismos anaerobios (Aguamarket, 2017).
            
              
        
          .py-3.py-md-4.color-tab.p-4.m-top-4.m-top-5(titulo="Etapas de la conversin biolgica")
            .row
              .col-md-4
                  figure
                    img(src='@/assets/curso/images/tema2/img_26.png', alt='Texto que describa la imagen')
              .col-md-8.mb-4.mb-md-0
                h4 Etapas de la conversin biolgica        
                p.mb-3 <b>La conversin biolgica de la materia orgnica de los lodos parece que se produce en tres etapas.</b> El primer paso del proceso comporta la transformacin por va enzimtica (hidrlisis) de los compuestos de alto peso molecular en compuestos que puedan servir como fuentes de energa y de carbono celular. El segundo paso (acidognesis) implica la conversin bacteriana de los compuestos producidos en la primera etapa en compuestos intermedios identificables de menor peso molecular. El tercer paso (metanognesis) supone la conversin bacteriana de los compuestos intermedios en productos finales ms simples, principalmente metano y dixido de carbono. En un digestor, la conversin de los lodos orgnicos y de los residuos se lleva a cabo mediante la accin conjunta de diferentes organismos anaerobios (Aguamarket, 2017).

          .py-3.py-md-4.color-tab.p-4.m-top-4.m-top-5(titulo="Hidrolizacin")
            .row
              .col-md-4
                  figure
                    img(src='@/assets/curso/images/tema2/img_27.png', alt='Texto que describa la imagen')
              .col-md-8.mb-4.mb-md-0
                h4 Hidrolizacin            
                p.mb-3 <b>Un grupo de microorganismos se ocupa de la hidrolizacin de los polmeros orgnicos y de los lpidos para formar elementos estructurales bsicos como los monosacridos, los aminocidos y los compuestos relacionados con estos.</b> Un segundo grupo de bacterias anaerobias fermenta los productos de la descomposicin para producir cidos orgnicos simples, de los que el que se presenta con mayor frecuencia en los digestores orgnicos es el cido actico. Este grupo de microorganismos, que reciben el nombre de no metanognicos, est formado por bacterias facultativas y anaerobias estrictas, aunque de forma colectiva se conocen como bacterias "formadoras de cidos (Aguamarket, 2017).

    
    .row.align-items-center.mb-5
      .col-lg-4(data-aos="fade-left") 
        .jumbotron-1.p-2
          p.text-white.text-center.mb-0 <b>Lechos bacterianos o filtros percoladores.</b>


    .row.justify-content-center.align-items-star.mb-5
      .col-lg-5(data-aos="fade-right").mb-3 
        .jumbotron.p-5.h-100 
          p.mb-3 Se trata de un sistema de depuracin biolgico aerobio cuyo funcionamiento se basa en hacer circular a travs de un medio poroso, aire y agua residual. Estn configurados en forma de cultivo fijo no sumergido y compuestos por un lecho, normalmente de gran dimetro, que puede estar formado por materiales naturales cantos rodados, coque metalrgico, escoria o antracita- o plsticos, y en cuya superficie se aplican de forma uniforme los lquidos residuales por medio de pulverizadores, y en forma de lluvia (SMA, 2015), ver figuras 25 y 26.
      .col-12.col-lg-5(data-aos="fade-left")
        .titulo-sexto.color-acento-contenido.mb-3
            h5 Figura 25
            span Filtro percolador
        img(src='@/assets/curso/images/tema2/img_28.png').mb-3 

    .row.justify-content-center.align-items-star.mb-3
      .col-lg-10
        .titulo-sexto.color-acento-contenido
          h5 Figura 26
          span Filtro percolador 
    .row.align-items-center.justify-content-center.mb-3
      .col-10.col-lg-10(data-aos="fade-left")
          img(src='@/assets/curso/images/tema2/fig_13.svg'  alt='')

    .row.justify-content-center.align-items-star.mb-3
      .col-lg-10
        .titulo-sexto.color-acento-contenido
          h5 Figura 27
          span Partes de un filtro percolador 
    .row.align-items-center.justify-content-center.mb-3
      .col-10.col-lg-10(data-aos="fade-left")
          img(src='@/assets/curso/images/tema2/fig_14.svg'  alt='')


    .row.align-items-center.mb-5
      .col-lg-6(data-aos="fade-left") 
        .jumbotron-1.p-2.pe-5
          p.text-white.text-center.mb-0 <b>Contactores biolgicos rotativos: biodiscos y biocilindros.</b>

    p.mb-3 Los contactores biolgicos rotativos estn basados en los procesos biolgicos aerobios. Estos requieren un contacto ntimo entre el agua residual, la biomasa activa y el oxgeno. Consisten en una serie de placas o discos colocados en un eje horizontal que giran lentamente dentro del tanque que contiene el agua residual, con un 49% de la superficie sumergida. Sobre la superficie de los discos se fija la biomasa que se encuentra alternativamente en contacto con el agua residual y el aire. Esto posibilita la ingestin de la materia orgnica por parte de los microorganismos y su posterior degradacin en presencia del oxgeno del aire (Cyclusid, 2020), ver figura 28. 

    .row.align-items-center.justify-content-center.mb-3
      .col-10.col-lg-10(data-aos="fade-left")
        .titulo-sexto.color-acento-contenido.mb-3
            h5 Figura 28
            span Unidad de tratamiento por biodiscos
    .row.justify-content-center.align-items-star.mb-0
      .col-12.col-lg-5(data-aos="fade-right") 
        img(src='@/assets/curso/images/tema2/img_29.png').mb-3 
      .col-12.col-lg-5(data-aos="fade-left")
        img(src='@/assets/curso/images/tema2/img_30.png').mb-3 

    .row.align-items-center.justify-content-center
      .col-lg-10.mt-3
        .jumbotron-3.p-2.mb-3
          p.mb-0 Nota. Tomado de Pedraza (2018). Unidad de tratamiento por biodiscos - feria FIMA. (Anexos / Figura 33a  Figura33b)


    .row.justify-content-evenly.align-items-star.p-2
      .col-lg-12(data-aos="fade-right")
        AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--gris")(data-aos="zoom-in-down")
          .row.justify-content-center.align-items-center(titulo="Los biocilindros")
            .col-md-2.mb-4.mb-md-0
              img(src='@/assets/curso/images/tema2/img_new_4.svg')
            .col-md-10.mb-4.mb-md-0
              p.mb-0  Son una variante de los biodiscos, su principal diferencia es la sustitucin de los discos por biocilindros, estn formados por una malla metlica, cuyo interior se rellena de piezas de plstico que sirven de soporte a la biomasa. En estos sistemas se produce un exceso de biomasa que se desprende del contactor, esta se mantiene en suspensin en el tanque gracias al movimiento del material soporte. Para regular la cantidad de microorganismos presentes en el sistema se dispone de un clarificador o decantador secundario, en l se produce la sedimentacin del exceso de biomasa (Cyclusid, 2020), ver figura 29.
          .row.justify-content-center.align-items-center(titulo="Caractersticas notables del proceso con biodiscos")
            .col-md-2.mb-4.mb-md-0
              img(src='@/assets/curso/images/tema2/img_new_5.svg')
            .col-md-10.mb-4.mb-md-0
              p.mb-3  Notable elasticidad, es decir, es capaz de absorber elevadas puntas de carga orgnica e hidrulica, manteniendo muy estables los niveles de salida. No requiere recirculacin de fangos; el fango activo del proceso es el que permanece adherido a los discos. Si la carga aumenta, la pelcula bacteriana crece espontneamente y los slidos en suspensin a la salida del biodisco disminuyen. Si la carga disminuye, la biomasa se desprende y aumentan los slidos en el efluente al decantador secundario. No es necesario controlar la concentracin del licor de mezcla, ya que el fango extrado es un desecho del proceso y ha de eliminarse como mejor convenga.
          .row.justify-content-center.align-items-center(titulo="Ruidos y otras variables")
            .col-md-2.mb-4.mb-md-0
              img(src='@/assets/curso/images/tema2/img_new_6.svg')
            .col-md-10.mb-4.mb-md-0
              p.mb-3  El nivel de ruidos es muy bajo, factor que es muy importante tanto ambientalmente como por higiene y salud del personal. Las dimensiones de los depsitos de oxidacin son menores que los utilizados por otros procesos. El rendimiento del proceso es ms estable en pocas fras, debido al hecho de permanecer cubiertos los tanques. El consumo energtico es muy reducido y a igualdad de resultados, comparndolo con un sistema de fangos activos, el consumo resulta ser la tercera parte Hidrometalica (s.f.).
          .row.justify-content-center.align-items-center(titulo="El funcionamiento de los biodiscos")
            .col-md-2.mb-4.mb-md-0
              img(src='@/assets/curso/images/tema2/img_new_7.svg')
            .col-md-10.mb-4.mb-md-0
              p.mb-3  Se divide en cuatro etapas, ver figura 30.
          .row.justify-content-center.align-items-center(titulo="El agua bruta")
            .col-md-12.mb-4.mb-md-0
              p.mb-3  El agua bruta de llegada entra a este tanque a travs de la tubuladura de entrada, donde se produce una primera sedimentacin de materia slida. Los slidos sedimentados se extraern peridicamente por parte del usuario en la primera etapa donde se realiza la primera toma de contacto entre el agua bruta de entrada y los discos. La superficie de los discos se coloniza de forma natural, formando un recubrimiento visible. En la segunda etapa de contacto, gracias a la concepcin del sistema de vertedero entre ambas etapas, se consigue un nivel de agua con menos fluctuaciones, favoreciendo el proceso y su salida hacia el tanque de sedimentacin secundaria. Con el proceso de dos etapas se consigue aumentar el crecimiento de la biomasa de forma equilibrada y constante. En el tanque de sedimentacin secundaria se produce la sedimentacin de la materia slida restante del proceso, a la cual se le realizan dos operaciones, la recirculacin y extraccin de sedimentos (Hidrometlica, s.f.). 
          
    .row.align-items-center
      .col-lg-5.mt-3
        .jumbotron-1.p-2.mx-2.mb-3
          p.mb-0.text-white Reactores biolgicos secuenciales (SBR).

    p.mb-5 Son reactores discontinuos en los que el agua residual se mezcla con un lodo biolgico en un medio aireado. Se trata de un proceso que se combina en un mismo tanque: aeracin/reaccin y clarificacin. La tecnologa de los reactores biolgicos secuenciales es una variante optimizada de la tecnologa convencional de lodos activados. Se basa en el uso de un solo reactor que opera en forma discontinua secuencial. El sistema de los reactores biolgicos secuenciales SBR consta de al menos cuatro procesos cclicos: llenado, aireacin, anoxia, decantacin y vaciado, tanto de efluente como de los lodos. Esta tecnologa es capaz de tolerar variaciones de carga y caudal y genera como producto lodos estabilizados, siendo en ocasiones, la tecnologa ms apropiada para la industria, ver figura 31 y 32. 


    .row.align-items-center.justify-content-center.mb-3
      .col-12.col-lg-5(data-aos="fade-right") 
        .titulo-sexto.color-acento-contenido.mb-3
              h5 Figura 31
              span Instalacin con tecnologa SBR
        img(src='@/assets/curso/images/tema2/img_32.svg').mb-3 
      .col-12.col-lg-5(data-aos="fade-left")
        .titulo-sexto.color-acento-contenido.mb-3
              h5 Figura 32
              span Planta de tratamiento de aguas residuales reactor biolgico secuencial (SBR) 
        img(src='@/assets/curso/images/tema2/img_33.svg').mb-3 

    p.mb-3 Se debe considerar que el tratamiento de aguas residuales mediante SBR se lleva a cabo en secuencia en 4 etapas:

    .row.justify-content-center.align-items-center.my-3
      .col-12.col-lg-12
        SlyderF.slyder-color.mb-5(columnas="col-lg-6 col-xl-4")(data-aos="zoom-in-down")
            .tarjeta.tarjeta_grey-1.p-4.h-100
              .row.justify-content-center.align-items-center
                img.mt-3(src='@/assets/curso/images/tema2/ico_new.svg',  alt='', style="width:200px; display:block; margin:auto 0 ;")
                .p-4
                  p.text-center.mt-0 Etapa de llenado: es cuando se aade el sustrato al reactor. Segn los objetivos previstos esta etapa puede ser esttica, mezclada o aireada.
            .tarjeta.tarjeta_grey-1.p-4.h-100
              .row.justify-content-center.align-items-center
                img.mt-3(src='@/assets/curso/images/tema2/ico_new.svg',  alt='', style="width:200px; display:block; margin:auto 0 ;")
                .p-4 
                  p.text-center.mt-0  Etapa de reaccin: las necesidades del tratamiento dictarn si el reactor se somete o no a aireacin. Aqu se proveen condiciones de mezcla en las que se permite el consumo de sustrato en condiciones ambientales controladas.
            .tarjeta.tarjeta_grey-1.p-4.h-100
              .row.justify-content-center.align-items-center
                img.mt-3(src='@/assets/curso/images/tema2/ico_new.svg',  alt='', style="width:200px; display:block; margin:auto 0 ;")
                .p-4 
                  p.text-center.mt-0 Sedimentacin: permite la separacin de slidos en condiciones de quietud. El resultado es un sobrenadante clarificado que se puede descargar como efluente. Generalmente, el tiempo de asentamiento vara entre media hora y una hora y media. Este proceso previene que el manto de slidos flote, debido a la acumulacin de gas.
            .tarjeta.tarjeta_grey-1.p-4.h-100
              .row.justify-content-center.align-items-center
                img.mt-3(src='@/assets/curso/images/tema2/ico_new.svg',  alt='', style="width:200px; display:block; margin:auto 0 ;")
                .p-4
                  p.text-center.mt-0 Vaciado: extraccin del agua clarificada del reactor. El exceso de lodo activado residual es removido; este proceso toma tiempo, puede tardar entre un 5% y un 30% del tiempo total (Domos agua, 2019).

    .row.align-items-center.mb-5
      .col-lg-4(data-aos="fade-left") 
        .jumbotron-1.p-2.pe-2
          p.text-white.text-center.mb-0 <b>Filtros verdes y humedales artificiales</b>


    .row.justify-content-center.align-items-star.mb-5
      .col-lg-7(data-aos="fade-right").mb-3 
        .jumbotron.p-5.h-100 
          p.mb-3 Un filtro verde consiste en una superficie de terreno donde se establece una plantacin forestal o agrcola, a la que se suministra, de forma recurrente y programada, aguas residuales urbanas. Una parte de estas aguas se evapora a la atmsfera y el resto es absorbido por las races de las plantas o es filtrada a travs del suelo (TECPA, 2018), ver figura 33.
      .col-12.col-lg-5(data-aos="fade-left")
        .titulo-sexto.color-acento-contenido.mb-3
            h5 Figura 33
            span Filtros verdes en depuracin de aguas residuales.
        img(src='@/assets/curso/images/tema2/img_28.png').mb-3 

    .row.justify-content-center.align-items-star.mb-5
      .col-lg-7(data-aos="fade-right").mb-3 
        .jumbotron.p-5.h-100 
          h3.mb-3 Filtros verdes     
          p.mb-3 Un filtro verde provoca procesos fsicos, qumicos y biolgicos naturales en el ecosistema suelo-agua. Estos procesos naturales reducen de forma considerable gran parte de los contaminantes del agua residual como: demanda biolgica de oxgeno (DBO5), demanda qumica de oxgeno (DQO), slidos suspendidos, nitrgeno, fsforo, y microorganismos patgenos (TECPA, 2018), ver figura 34.
      .col-12.col-lg-5(data-aos="fade-left")
        .titulo-sexto.color-acento-contenido.mb-3
            h5 Figura 34
            span Tratamiento de aguas residuales mediante humedales artificiales
        img(src='@/assets/curso/images/tema2/img_28.png').mb-3 

    h3.mb-3 Depuracin de los filtros verdes     

    p.mb-3 De acuerdo con la Universidad Veracruzana los mecanismos de depuracin de los filtros verdes son, en primera instancia, los procesos fsicos: granulometra y textura. Luego, procesos qumicos: pH, capacidad de intercambio inico, condiciones de aireacin y encharcamiento. En tercer lugar, procesos biolgicos: races, microorganismos (Universidad Veracruzana, s.f.), ver figura 35.

    .row.justify-content-center.align-items-star
      .col-lg-8
        .titulo-sexto.color-acento-contenido
          h5 Figura 35
          span Bioingeniera
    .row.align-items-center.justify-content-center
      .col-10.col-lg-8(data-aos="fade-left")
          img(src='@/assets/curso/images/tema2/fig_15.svg'  alt='')

    .row.align-items-center.mb-5
      .col-lg-4(data-aos="fade-left") 
        .jumbotron-1.p-2.pe-2
          p.text-white.text-center.mb-0 <b>Reactor biolgico de membrana (MBR).</b>

    p.mb-3 (Reactor biolgico + ultrafiltracin) se incluyen en las denominadas tecnologas de membrana, las cuales han experimentado un gran desarrollo en la ltima dcada. La aplicacin de estas tecnologas a los MBR permite la separacin del fango y el lquido mediante membranas, obteniendo ventajas importantes frente a la separacin en los tradicionales decantadores secundarios. El aumento de la demanda de agua ha impulsado la implantacin de estos sistemas a escala real, especialmente en aquellos casos en que se plantea la posibilidad de reutilizacin de agua (Universidad de Alcal, 2007), ver figura 36.     


    .row.justify-content-center.align-items-star
      .col-lg-8
        .titulo-sexto.color-acento-contenido
          h5 Figura 36
          span Biorreactor de membrana


    .row.align-items-center.justify-content-center
      .col-10.col-lg-8(data-aos="fade-left")
          img(src='@/assets/curso/images/tema2/fig_16.svg'  alt='')

    .row.align-items-center.align-items-center.my-5
      .col-lg-12
        TabsA.color-acento-contenido(data-aos="fade")
          .tarjeta.back-tab.px-5.py-4(titulo="Agua filtrada por membrana")
              .row.px-2.py-2.justify-content-center
                .col-12.col-lg-4
                .col-lg-8
                  h4 Agua filtrada por membrana    
                  p.mb-0 Su funcionamiento se basa en que el agua del reactor biolgico es filtrada pasando a travs de las paredes de una membrana, debido a una pequea depresin producida por una bomba centrfuga. El agua filtrada es extrada del sistema mientras el fango y los compuestos de tamao superior al poro de la membrana quedan retenidos y permanecen o retornan al reactor biolgico (Universidad de Alcal, 2007).
                
          
          .tarjeta.back-tab.px-5.py-4(titulo="Contralavado")
            .row.px-2.py-2.justify-content-center
              .col-12.col-lg-4
              .col-lg-8
                h4 Contralavado             
                p.mb-0 Este ciclo se alterna con un corto contralavado, en el que se invierte el sentido del flujo para forzar el paso del agua filtrada desde el interior al exterior de la membrana para limpiarla. Peridicamente, en funcin del grado de ensuciamiento se realizan limpiezas qumicas en profundidad de las membranas mediante su inmersin en una solucin de limpieza (Universidad de Alcal, 2007).
              

          .tarjeta.back-tab.px-5.py-4(titulo="MBR y sus partes ")
            .row.px-2.py-2.justify-content-center
              .col-12.col-lg-4
              .col-lg-8
                h4 MBR y sus partes              
                p.mb-0 De acuerdo con Remtamavares, los MBR estn compuestos por dos partes principales:
                p.mb-3 - Reactor biolgico: responsable de la degradacin de los compuestos presentes en el agua residual.
                p.mb-3 - Mdulo de membranas: encargado de llevar a cabo la separacin fsica del licor de mezcla (Universidad de Alcal, 2007).
              
    h3.mb-3 Configuraciones bsicas     

    p.mb-3 Membranas o sistemas sumergidos: las membranas se sitan dentro del propio reactor biolgico, eliminando las necesidades de bombeo y aprovechando la agitacin mecnica de la aireacin (Universidad de Alcal, 2007), ver figura 37.

    .row.justify-content-center.align-items-star
      .col-lg-8
        .titulo-sexto.color-acento-contenido
          h5 Figura 37
          span Membranas sumergidas


    .row.align-items-center.justify-content-center
      .col-10.col-lg-8(data-aos="fade-left")
          img(src='@/assets/curso/images/tema2/fig_17.svg'  alt='')
      
    .row.align-items-center.justify-content-center
      .col-lg-8.mt-3
        .jumbotron-3.p-2.mb-3
          p.mb-0 Nota. Tomado de la Universidad de Alcal (2007). 


