      .h2 2.4 Tratamientos terciarios del agua residual

    p.mb-3 La finalidad de los tratamientos terciarios es eliminar la carga orgnica residual y aquellas otras sustancias contaminantes no eliminadas en los tratamientos secundarios, como por ejemplo, los nutrientes, fsforo y nitrgeno. Estos procesos son de naturaleza biolgica o fisicoqumica, siendo el proceso unitario ms empleado el tratamiento fisicoqumico. Este consta de una coagulacin  floculacin y una decantacin (Cyclusid depuracin industrial, s.f.).

    p.mb-3 Otros procesos empleados como tratamientos terciarios son las resinas de intercambios de iones, la adsorcin en carbn activo, la ultrafiltracin, la smosis inversa, la electrodesinfeccin, las membranas cermicas, etc. 

